@import url('https://fonts.googleapis.com/css2?family=Merriweather&display=swap');

  .purple-box {
    position: fixed;
    width: 43%;
    background-color: #b8baf2; 
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 5%;
    right: 0;
  }

  /* @keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
  
  @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: rgb(0, 0, 0); }
  } */

  .intro-container {
    width: 80%;
    display: flex;
    align-items: center;
    padding-top: 2%;
    padding-left: 5%;
    padding-right: 5%;
    height: 96vh;
    overflow: hidden;
  }

  @keyframes fadeUp {
    0% {
      opacity: 0;
      transform: translateY(20px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }

  .intro-text {
    color: black;
    font-size: 36px;
    font-family: Poppins, sans-serif;
    animation: fadeUp 1.5s ease-out;
    width: 50%;
    text-align: left;
}

  .text {
    color: black;
    font-size: 20px;
    font-family: Poppins, sans-serif;
    animation: fadeUp 1.5s ease-out;
    width: 100%;
    text-align: left;
  }

  .kara-img{
    position: absolute;
    justify-content: center;
    top: 32%;
    left: 67%;
    width: 300px;
    height: auto;
    filter: grayscale(35%);
    animation: fadeUp 1.5s ease-out;
  }
  
  .text-container {
    display: flex;   
    flex-direction: column;
    padding-top: 7%;
    padding-left: 300px;
    padding-right: 100px;
  }

  h1 {
    align-self: center;
    font-family: Poppins, sans-serif;
    font-weight: 500;
    font-size: 40px;
  }

  h2 {
    font-family: Poppins, sans-serif;
    font-weight: 500;
    font-size: 30px;
    align-self: center;
    margin-top: 3%;
    text-decoration: underline
  }

  h3 {
    font-family: Poppins, sans-serif;
    font-weight: 500;
    font-size: 24px;
  }

  p {
    margin-top: -0.5%;
    font-size: 18px;
    font-family: Poppins, sans-serif;
    line-height: 1.8
  }

  html {
    scroll-behavior: smooth;
    scroll-padding-top: 110px;
  }

  .side-menu {
    position: fixed;
    margin-left: 1%;
    margin-top: 8%;
    width: 250px;
    background-color: #b8baf2;
    box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.25);
    height: 83%;
    border-radius: 20px;
  }

  .side-menu ul {
    list-style-type: none;
    padding: 0;           
    margin: 0;            
  }
  
  .side-menu ul li {
    text-align: left;   
    margin: 30px 0;  
    padding-left: 9%
  }
  
  .side-menu ul li h3 {
    font-size: 18px;
    margin-top: -10px;
    margin-bottom: -10px;
    padding-left: 20px;
    text-decoration: none; 
    color: #000000;      
    font-family: Poppins, sans-serif; 
    transition: transform 0.3s ease-in-out; 
  }

  .side-menu ul li a {
    text-decoration: none; 
    color: #000000;      
    font-size: 22px;    
    font-family: Poppins, sans-serif; 
    transition: transform 0.3s ease-in-out; 
    display: inline-block;
  }
  
  .side-menu ul li a:hover {
    transform: scale(1.1);
  }
  
  .side-menu ul li h3:hover {
    transform: scale(1.1);
  }
  
  .image-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap
  }
  
  .google-img {
    width: 400px;
    height: auto;
  }

  .google-img-mobile {
    width: 200px;
    height: auto;
  }

  .ig-img {
    width: 200px;
    height: auto;
  }

  figure {
    position: relative;
    display: inline-block;
  }

  figcaption {
    font-family: Poppins, sans-serif; 
    text-align: center;
    font-size: 15px;
  }

  .input-table {
    width: 801px;
    height: 575px;
    border: none;
    outline: none;
  }

  .output-table {
    width: 825px;
    height: 745px;
    border: none;
    outline: none;
  }

  .navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    color:#3B64B8;
    margin-top:0px;
    z-index: 999;
    position: fixed;
    width: 100%;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
  }
  
  .navbar-brand a {
    color:#3B64B8;
    text-decoration: none;
  }

  .navbar-brand img {
    height: 70px;
    width: auto;
    padding-left: 2.5rem;
  }

  .navbar-brand:hover {
    transform: scale(1.1);
    transition: transform 0.3s ease-in-out;
  }
  
  .navbar-links {
    list-style: none;
    display: flex;
    gap: 1rem;
    font-size: 30px;
    text-align: center;
    position: center;
  }

  .navbar-links:first-child {
    margin-right: 5%;
  }
  
  .navbar-links:last-child {
    margin-left: 5%;
  }
  
  .navbar-links li a {
    color: #000000;
    text-decoration: none;
    font-family: Poppins, sans-serif;
  }
  
  .navbar-links:hover {
    transform: scale(1.1);
    color: #000000;
    transition: transform 0.3s ease-in-out;
  }

  .navbar-links li a.active {
    text-decoration: underline;
    text-decoration-thickness: 1.5px;
  }

  .settings-container {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
  }
  
  .dropdown-container {
    width: 450px;
    padding-left: 100px;
    display: flex;
    flex-direction: column;
  }
  /* Style for the dropdown */
  .select-dropdown {
    margin-top: 50px;
  }
  
  /* Style for the selected option text */
  .selected-option {
    margin-top: 20px;
    font-weight: light;
    color: #949494;
    font: 'Montserrat', sans-serif;
    font-size: 30px;
    padding-left: 50px;
  }

  .custom-search-container {
    position: relative;
    padding: 10px;
    border-bottom: 1px solid #ddd;
    background-color: white;
    z-index: 10; /* Ensure it's above other elements */
  }
  
  .custom-search-input {
    width: 100%;
    padding: 8px;
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    border: 5px solid #ddd;
    border-radius: 4px;
    box-sizing: border-box; /* Ensures padding and border are included in width */
  }

  .pdf-viewer {
    width: 825px;
    height: 600px;
    margin-top: 40px;
    display: flex;
    overflow-y: scroll; 
    flex-direction: column;
    border: 2px solid #3B64B8;
    margin-left: 100px;
  }
  
  .pdf-viewer .react-pdf__Page {
    position: absolute;
    display: block;
    top: 0;
    margin-left: 100;
    max-height: 600px;
  }

  .pdf-viewer .react-pdf__Page .text-layer {
    display: none;
  }
  
  .page-info {
    text-align: center;
    margin-top: 10px;
    font-family: 'Montserrat', sans-serif;
    color: #3B64B8;
  }
  .hospital-table {
    width: 100%;
    border-collapse: collapse;
    margin: 20px 0;
    font-size: 18px;
    text-align: left;
  }
  
  .hospital-table th, .hospital-table td {
    padding: 12px 15px;
    border-bottom: 1px solid #ddd;
  }
  
  .hospital-table th {
    background-color: #3B64B8;
    color: white;
  }
  
  .hospital-table tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  
  .hospital-table tr:hover {
    background-color: #ddd;
  }

  .progress-bar {
    margin-top: 10px;
    background-color: #ffffff;
    width: 70%;
    border-radius: 18px;
    margin: auto;
    border-color:#3B64B8;
    border-width: 2px;
    border-style: solid;
  }

  .progress-bar-body {
    background-color: #3B64B8;
    border-radius: 15px;
    color: white;
    padding: 5px; /* Add padding around the text */
    box-sizing: border-box; /* Ensure the padding doesn't affect the width */
    width: 25%; /* Adjust based on the current progress (still applies to the width of the bar) */
    text-align: center; /* Center the text inside the bar */
}

.deductible-label {
  margin-left: 230px;
  margin-top: 30px;
  margin-bottom: 15px;
  display: inline-block;
  position: relative; /* Ensure the tooltip stays within this label */
}

.deductible-label sup {
  cursor: pointer;
  position: relative; /* Tooltip will be positioned relative to this */
  color:#3B64B8;

}

.tooltiptext {
  visibility: hidden;
  width: 180px;
  background-color: #3B64B8;
  color: #fff;
  text-align: center;
  border-radius: 5px;
  padding: 5px;
  position: absolute;
  z-index: 1;
  bottom: 150%; /* Adjust this value to control the tooltip's vertical position */
  left: 50%;
  transform: translateX(-50%); /* Center the tooltip horizontally */
  opacity: 0;
  transition: opacity 0.3s;
}

.deductible-label sup:hover .tooltiptext {
  visibility: visible;
  opacity: 0.9;
}

.header-container {
  display: flex;
  justify-content: center; /* Horizontal centering */
  margin-bottom: 0px;
  margin-top: 10px;
}

.header {
  font-family: 'Montserrat', sans-serif;
  color: #3B64B8;
  font-size: 20px;
}

#tooltip2 {
  color: black;
  position: absolute;
  visibility: hidden; /* Initially hidden */
  background-color: white;
  padding: 5px;
  border-radius: 3px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
  z-index: 100; /* Make sure it's above the map */
  pointer-events: none; /* Prevent mouse interaction */
  white-space: nowrap;
}

